.ChartBody {
  flex-grow: 1;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 82.635%;
  &-Columns {
    width: 100%;
    height: 70.4698%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }
  &-Users {
    @include media(margin-top, (24px, 24px, 40px));
    width: 85.8696%;
    height: 13.4228%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.Column {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;

  height: 100%;
  @include media(width, (30px, 40px, 60px, 68px, 90px));
  &-Title {
    text-align: center;
  }
  &-Value {
    position: absolute;
    left: 50%;
    transform: translate(-50%, calc(-100% - 8px));
  }
  &-Line {
    @include media(border-radius, (6px, 6px, 12px, 12px, 20px));
    @include media(min-height, (8px, 8px, 8px, 8px, 8px));
    @include media(margin-bottom, (12px, 12px, 12px, 12px, 12px));
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%),
      radial-gradient(149.08% 93.3% at 38.75% 72.1%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2),
      inset 2px 2px 16px rgba(103, 103, 103, 0.6);
    &_active {
      background: radial-gradient(81.14% 100% at 50% 0%, #ffa300 0%, #2d1c00 100%);
      box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4),
        inset 0px 6px 15px rgba(255, 162, 0, 0.5);
    }
  }
}

@media (orientation: portrait) {
  .ChartBody_landscape {
    display: none;
  }
  .ChartBody {
    width: 100%;
    &-Columns {
      height: 72.542%;
      padding-right: 16px;
      transform: translate(-8px);
    }
    &-Users {
      @include mediaPortrait(margin-top, (24px, 24px, 24px, 40px));
      width: 87.234%;
      height: auto;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      .Divider {
        @include mediaPortrait(margin, (10px 0, 16px 0, 16px 0));
      }
    }
  }
  .Column {
    @include mediaPortrait(width, (30px, 40px, 40px, 60px));
  }
}

@media (orientation: landscape) {
  .ChartBody_portrait {
    display: none;
  }
}

.theme_light {
  .Column-Line {
    background: linear-gradient(
      180deg,
      rgba(244, 244, 244, 0.9) 0.82%,
      #e9e9e9 100%,
      rgba(234, 234, 234, 0) 100%
    );
    box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5),
      inset 0px 0px 20px rgba(207, 207, 207, 0.5);

    &_active {
      background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
      box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5),
        inset 0px 0px 20px rgba(255, 176, 57, 0.8);
    }
  }
}
